import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs/Observable";
import { Injectable } from "@angular/core";
import { SellerSuggestionData } from "./sell-suggestion-data";

@Injectable()
export class SellService {

  constructor(private _http:HttpClient){

  }

  /**
   *
   * @returns {Observable<any[]>}
   */
  getTags(title:string, categoryId : number):Observable<SellerSuggestionData[]>{

    return this._http.get<SellerSuggestionData[]>("http://localhost:5000/get_data?categoryId="
    + categoryId + "&title="+ title);

    // return this._http.get<SellerSuggestionData[]>("./assets/data/sell-suggestion-data.ts");
  }


}



