import { Component, Input, OnInit, SimpleChange, SimpleChanges } from '@angular/core';
import { SellService } from "../Services/SellService/sell-service";

@Component({
  selector: 'app-sell',
  templateUrl: './sell.component.html',
  styleUrls: ['./sell.component.css']
})
export class SellComponent implements OnInit {

  price:number;
  percentageOfSelling:number = 30;

  priceRange: number[] = [0,10];

  selectedPriceInSlider : number = 50;

  image: any;
  categoryId:any;
  title:any;
  description:any;

  chooseTitleAlert: boolean = false;
  chooseCategoryAlert: boolean = false;

  tags: string[]=[];

  suggestionKeywords: string[]=[];
  suggestionPriceRange: number[]=[];


  constructor(private _sellService:SellService){
  }


  ngOnInit(){
  }

  /**
   *
   * @param {number} selectedPrice
   */
  calculatePercentage(selectedPrice : number){

    if(selectedPrice < this.suggestionPriceRange[0]){
      this.percentageOfSelling = 100;
    }else if(selectedPrice < this.suggestionPriceRange[0]){
      this.percentageOfSelling = 75;
    }else if(selectedPrice < this.suggestionPriceRange[0]){
      this.percentageOfSelling = 50;
    }else if(selectedPrice < this.suggestionPriceRange[0]){
      this.percentageOfSelling = 25;
    }else{
      this.percentageOfSelling = 1;
    }

  }

  /**
   *
   * @param {number} selectedPrice
   */
  actOnSelectedPriceValue(selectedPrice:number){

    // set the value in slider
    this.selectedPriceInSlider = selectedPrice;

    // calculate the percentage
    this.calculatePercentage(selectedPrice);
  }
  /**
   *
   */
  getPriceSuggestion(){

    if(this.price != 0 && this.price != null) {

      if ( this.price > 700 ) {
        this.percentageOfSelling=30;
      }else if ( this.price > 600 ) {
        this.percentageOfSelling=45;

      }else if ( this.price > 400 ) {
        this.percentageOfSelling=75;

      }else if ( this.price > 200 ) {
        this.percentageOfSelling=100;

      }

    }

  }


  /**
   *
   * @param event
   */
  selectImage(event){
    const file=event.target.files.item(0)

    if ( file.type.match('image.*') ) {
      if ( event.target.files && event.target.files[0] ) {
        var reader=new FileReader();

        reader.readAsDataURL(event.target.files[0]); // read file as data url

        reader.onload=(event) =>{ // called once readAsDataURL is completed
          this.image=event.target;
        }
      }
    }else {
      alert('invalid format!');
    }
  }

  /**
   *
   * @param tag
   */
  addSuggestedTag(tagValue:string, i:number){

    var isAlreadyThere = false;

    this.tags.forEach(tag => {
      if(tag == tagValue){
        isAlreadyThere = true;
      }
    })

    console.log("tagValue " + tagValue);
    if ( tagValue != "" && isAlreadyThere == false ) {

      // push the text
      this.tags.push(tagValue);

      // this.tags.splice(i);
    }

  }
  /**
   *
   * @param {string} tag
   */
  addTag(tag: any){

    if ( tag.target.value != "" ) {

      // push the text
      this.tags.push(tag.target.value);
      //clear the text
      tag.target.value="";
    }

  }

  /**
   *
   * @param categoryId
   */
  setCategory(categoryId:any){
    console.log("categoryId id " + categoryId);
    if(categoryId != "") {
      this.categoryId=categoryId;
    }
  }

  /**
   *
   */
  validate(){

    this.tags = [];
    // set the price

    // check all datas are entered.

    console.log("title and categoryId " + this.title +
    this.categoryId);

    if(this.categoryId != "" && this.categoryId != null && this.title != "" && this.title != null){
     // send rest
      this._sellService.getTags(this.title, this.categoryId).subscribe((data) =>{
        this.suggestionKeywords = data[0].keywords;
        this.suggestionPriceRange = data[0].price_range;
        this.price = data[0].price_range[1];
        this.priceRange[0] = data[0].price_range[0];
        this.priceRange[1] = data[0].price_range[1];
      });
      //set alert box view
      this.setAlertBoxView(false);
      console.log("yes" );

    }else{
      console.log("no" );

      if(this.title != "" && this.title != null) {
        this.chooseTitleAlert=true;
      }

      if(this.categoryId != ""){
        this.chooseCategoryAlert=true;
      }
        // alert in front end that it is not fulfilled.
      this.setAlertBoxView(true);
   }
  }

  /**
   *
   */
   setAlertBoxView(canShow : boolean){
    this.chooseTitleAlert=canShow;
    this.chooseCategoryAlert=canShow;
  }
}



